<template>
  <div id="app">
    <header class="header">
      <div class="container d-flex jcsb">
        <div>
          <a href="#" class="header-logo">
            Game
            <span>Box</span>
          </a>
          <h1 class="header-gameTitle">God of war</h1>
          <div class="header-gameInfo">
            <span class="header-gameInfo_date">20th Apr, 2018</span>
            <span class="header-gameInfo_studio">SIE Santa Monica Studio</span>
          </div>
          <a href="#" class="header-btn">View info</a>
        </div>
        <div class="header-gameRating d-flex jcsb">
          <div>
            <h4 class="header-gameRating_title">Rating</h4>
            <p class="header-gameRating_base">
              Based on 603
              <br />member ratings
            </p>
          </div>
          <span class="header-gameRating_value">96</span>
        </div>
      </div>
    </header>

    <section class="games">
      <div class="container">
        <h2 class="games-title">TOP 100</h2>

        <div class="games-cards">
          <div class="games-card" v-for="(card,index) in gamesCard" :key="index">
            <div class="games-card_img">
              <img :src="card.src" :alt="card.alt" />
              <span>{{ card.year }}</span>
            </div>
            <div class="games-cardBody d-flex jcsb">
              <div>
                <h3 class="games-cardBody_title">{{ card.title }}</h3>
                <p class="games-cardBody_genre">{{ card.genre }}</p>
              </div>
              <div class="games-cardBody_rating">{{ card.rating }}</div>
            </div>
          </div>
        </div>
      </div>
    </section>

    <footer class="footer">
      <div class="container">
        <div class="d-flex jcsb aic">
          <a href="#" class="footer-logo">
            Game
            <span>Box</span>
          </a>
          <ul class="footer-menu">
            <li>
              <a href="#" class="footer-menu_link">About</a>
            </li>
            <li>
              <a href="#" class="footer-menu_link">Games</a>
            </li>
            <li>
              <a href="#" class="footer-menu_link">Ratings</a>
            </li>
            <li>
              <a href="#" class="footer-menu_link">Contact</a>
            </li>
          </ul>
        </div>
        <hr />
        <div class="d-flex jcsb aic">
          <span class="designed">Designed by ITL</span>
          <div class="footer-social">
            <a href="#">
              <img src="/img/facebook.svg" alt="facebook" />
            </a>
            <a href="#">
              <img src="/img/pinterest.svg" alt="pinterest" />
            </a>
            <a href="#">
              <img src="/img/twitter.svg" alt="twitter" />
            </a>
            <a href="#">
              <img src="/img/linkedin.svg" alt="linkedin" />
            </a>
          </div>
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "app",
  data() {
    return {
      gamesCard: [
        {
          title: "God of War",
          genre: "Adventure, Role-playing (RPG)",
          rating: 96,
          year: 2018,
          src: "/img/card-img.png",
          alt: "godofwar"
        }
      ],
      page: 1,
      perPage: 9,
      pages: [],
      API_KEY: "a46549452918dfeab1f37161286dd77d",
      baseUrl: "https://api-v3.igdb.com/",
      local: "http://localhost:8080/"
    };
  },
  created() {
    this.getGamesCard();
  },
  methods: {
    getGamesCard() {
      axios("https://api-v3.igdb.com/games", {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Access-Control-Allow-Origin": "*",
          "user-key": this.API_KEY
        },
        data: "fields name,slug,genres,total_rating;"
      })
        .then(response => {
          console.log("--- success ----");
          console.log(response);
          this.gamesCard = response.data;
        })
        .catch(response => {
          console.log("--- error ----");
          console.log(response);
        });
    }
  }
};
</script>




